      .domain([0, temprange.length - 1])\n            .range([0, width - padding.left - padding.right]);\n\n        let yScale = d3.scaleLinear()\n            .domain([0, d3.max(dataset)])\n            .range([height - padding.top - padding.bottom, 0]);\n\n        let xAxis = d3.axisBottom()\n            .scale(xScale);\n\n        let yAxis = d3.axisLeft()\n            .scale(yScale);\n\n        let rectPadding = 4;\n        let rects = svg.selectAll('.MyRect')\n            .data(dataset)\n            .enter()\n            .append(\"rect\")\n            .attr('class', 'MyRect')\n            .attr('transform', 'translate(' + padding.left + ',' + padding.top + ')')\n            .attr('x', function (d, i) {\n                return xScale(i) + rectPadding / 2;\n                ///return xScale(i);\n                // return scaleBand(i);\n            })\n            .attr('y', function (d) {\n                //return yScale(d);\n                var min = yScale.domain()[0];\n                return yScale(min);\n\n            })\n            .attr('fill', 'steelblue')\n            .attr('width', 20)\n            .attr(\"height\", function (d) {\n                return 0;\n            })\n            .transition()\n            .delay(function (d, i) {\n                return i * 200;\n            })\n            .duration(2000)\n            .ease(d3.easeCubicInOut)\n            .attr(\"y\", function (d) {\n                return yScale(d);\n            })\n            .attr(\"height\", function (d) {\n                return height - padding.top - padding.bottom - yScale(d);\n            });\n\n        var texts = svg.selectAll(\".MyText\")\n            .data(dataset)\n            .enter()\n            .append(\"text\")\n            .attr(\"class\", \"MyText\")\n            .attr(\"transform\", \"translate(\" + padding.left + \",\" + padding.top + \")\")\n            .attr(\"x\", function (d, i) {\n                return xScale(i) + rectPadding / 2;\n            })\n            .attr(\"dx\", function (d, i) {\n                return 0;\n            })\n            .attr(\"dy\", function (d) {\n                return 20;\n            })\n            .text(function (d) {\n                return d;\n            })\n            .attr(\"y\", function (d) {\n                var min = yScale.domain()[0];\n                return yScale(min);\n            })\n            .transition()\n            /*.ease('bounce')*/\n            .delay(function (d, i) {\n                return i * 200;\n            })\n            .duration([2000])\n            .ease(d3.easeLinear)\n            .attr(\"y\", function (d) {\n                return yScale(d);\n            });\n\n        //添加x轴\n        svg.append(\"g\")\n            .attr(\"class\", \"axis\")\n            .attr(\"transform\", \"translate(\" + padding.left + \",\" + (height - padding.bottom) + \")\")\n            .call(xAxis);\n\n        //添加y轴\n        svg.append(\"g\")\n            .attr(\"class\", \"axis\")\n            .attr(\"transform\", \"translate(\" + padding.left + \",\" + padding.top + \")\")\n            .call(yAxis);\n\n        let svgDom = svg.node();\n\n        d3.select('svg')\n            .selectAll('.MyRect')\n            /*.on('mouseover',function(d,i){\n                let x = d3.mouse(svgDom)[0]+20;\n                let y = d3.mouse(svgDom)[1];\n                d3.select('.popup')\n                    .style('left',x+'px')\n                    .style('top',y+'px')\n                    .style('display','block')\n                    .select('.no')\n                    .text(d)\n            })\n           .on('mouseout',function (d,i) {\n               d3.select('.popup')\n                   .style('display','none')\n           })*/\n            .on(\"mouseover\", function (d, i) {\n                d3.select(this)\n                    .attr(\"fill\", \"yellow\");\n            })\n            .on(\"mouseout\", function (d, i) {\n                d3.select(this)\n                    .transition()\n                    .duration(500)\n                    .attr(\"fill\", \"steelblue\");\n            });\n    }\n\n    /**\n     * 画饼图\n     * */\n    drawPie() {\n        let width = 400;\n        let height = 400;\n        /*提供画布*/\n        let svg = d3.select('body')\n            .append('svg')\n            .attr('width', width)\n            .attr('height', height);\n        /*提供数据*/\n        let dataset = [10, 20, 30];\n\n        /*提供布局，数据转换*/\n        let pie = d3.pie();\n        let piedata = pie(dataset);\n\n        console.log(piedata);\n\n        let color = ['green', 'red', 'black'];\n        let outRadius = 150;\n        let innerRadius = 80;\n        /*弧生成器*/\n        let arc = d3.arc()\n            .innerRadius(innerRadius)\n            .outerRadius(outRadius);\n        let arcOver = d3.arc()\n            .innerRadius(innerRadius)\n            .outerRadius(outRadius + 10);\n\n        /* arc 可以当做一个函数使用，把 piedata 作为参数传入，即可得到路径值。*/\n\n        /* svg中添加相应的足够数量的组元素 g*/\n        let arcs = svg.selectAll('g')\n            .data(piedata)\n            .enter()\n            .append('g')\n            .style('cursor', 'pointer')\n            .attr(\"transform\", \"translate(\" + (width / 2) + \",\" + (width / 2) + \")\");\n        /*在g中生成path*/\n        arcs.append('path')\n            .attr('fill', function (d, i) {\n                return color[i];\n            })\n            .attr('d', function (d) {\n                return arc(d);\n            })\n            .on('mouseover', function (d, i) {\n                d3.select(this).attr('d', function (d) {\n                    return arcOver(d);\n                })\n            })\n            .on('mouseout', function (d, i) {\n                d3.select(this).attr('d', function (d) {\n                    return arc(d);\n                });\n            });\n        /*在g中生成tex*/\n        arcs.append('text')\n            .attr('transform', function (d) {\n                return 'translate(' + arc.centroid(d) + ')';\n            })\n            .attr('text-anchor', 'middle')\n            .attr('fill', '#fff')\n            .text(function (d) {\n                return '' + d.data + ',' + ((d.data / 60).toFixed(2)) * 100 + '%';\n            })\n    }\n\n\n\n\n\n\n          ")]),_vm._v("\n        ")])])])}]
